* {
  font-size: large;
}

.dropdown {
  position: relative;
  color: #333;
  cursor: default;
}

.dropdown .down__arrow {
  border-color: #999 transparent transparent;
  border-style: solid;
  border-width: 5px 5px 0;
  content: " ";
  display: block;
  height: 0;
  margin-top: 0.3rem;
  position: absolute;
  right: 10px;
  top: 14px;
  width: 0;
}

.dropdown .down__arrow.open {
  border-color: transparent transparent #999;
  border-width: 0 5px 5px;
}

.dropdown .selected__value {
  line-height: 1.5;
  font-size: 1rem;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 2px;
  box-sizing: border-box;
  cursor: default;
  outline: none;
  padding: 8px 52px 8px 10px;
  transition: all 200ms ease;
  width: 100%;
}

.search__box .search {
  display: block;
  min-width: 90%;
  height: 25px;
  margin: 10px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 2px;
}

.add__button {
  float: right;
  display: block;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 2px;
}

.add__button:hover {
  cursor: pointer;
  color: #333;
}

.more__items {
  float: right;
}

.dropdown .options {
  display: none;
  background-color: #fff;
  border: 1px solid #ccc;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
  box-sizing: border-box;
  margin-top: -1px;
  max-height: 200px;
  overflow-y: auto;
  position: absolute;
  top: 100%;
  width: 100%;
  z-index: 1000;
}

.dropdown .options.open {
  display: block;
}

.dropdown .option {
  box-sizing: border-box;
  color: rgba(51, 51, 51, 0.8);
  cursor: pointer;
  display: block;
  padding: 8px 10px;
}

.dropdown .option.selected__value,
.dropdown .option:hover {
  color: #333;
}
